<template>
  <div class="container-fluid">
    <nav class="navbar navbar-expand fixed-top navbar-light mb-5" style="background-color:#e3f2fd;">
      <span class="justify-content-start"><button class="btn btn-primary btn-sm mt-1 pt-0 pb-0" style="font-size:10px;" @click="deleteflg = !deleteflg">一行<br>削除</button></span>
      <span class="justify-content-start ml-1"><button class="btn btn-primary btn-sm mt-1 pt-0 pb-0" style="font-size:10px;" v-on:click="clearAll()">全<br>削除</button></span>
      <span class="justify-content-center ml-4 mr-5"><h5>買い物リスト</h5></span>
      <div></div>
      <span class="justify-content-end ml-2"><button class="btn btn-primary btn-sm mt-1 pt-0 pb-0" style="font-size:10px;" v-on:click="buy()">買った<br>よ☆</button></span>
    </nav>

    <div class="overflow-auto mt-5" style="height:450px;">
      <div class="form-inline rounded  border border-info mt-2 mb-1" v-for="(todo,i) in dogs"  v-bind:key="i">
        <span class="text-center col-2"><img v-bind:src=todo.id width="20" ></span>
        <span class="text-left col-7">{{todo.name}}</span>
        <span v-show="!deleteflg" class="col-3"><input type="checkbox" v-bind:id=todo.name v-bind:value=todo.name v-bind:checked=todo.kago v-model="buycheckboxs" @change="onChangeEventHandler(i)" data-toggle="toggle" data-on="かごに<br>いれたよ！" data-off="まだかって<br>ないよ" data-onstyle="info" data-offstyle="secondary"></span>
        <span v-show="deleteflg" class="col-3"><button class="btn btn-danger mt-1 pt-0 pb-0 btn-sm" v-on:click="deleteTodo(i)">削除</button></span>
      </div>
    </div>
    <nav class="navbar fixed-bottom navbar-light mt-5" style="background-color:#FFFFFF;">
      <div class="row">
        <span class="col-4"></span>
        <span class="col-4 text-center"><input type="image" v-bind:src="plusbutton" width="30" height="30" v-on:click="goPageAdd"/></span>
        <span class="col-4"></span>
        <span class="w-100"></span>
        <span class="text-center col-4 h-auto"><input type="image" v-bind:src="favorite" width="60" height="60" v-on:click="goPageFavorite"/></span>
        <span class="text-center col-4 h-auto"><input type="image" v-bind:src="boughtlist" width="60" height="60" v-on:click="goPageCheckList"/></span>
        <span class="text-center col-4 h-auto"><input type="image" v-bind:src="history" width="60" height="60" v-on:click="goPageHistory"/></span>
      </div>
    </nav>
  </div>
</template>

<script src="https://cdn.jsdelivr.net/gh/gitbrent/bootstrap4-toggle@3.6.1/js/bootstrap4-toggle.min.js"></script>

<script>
    
    export default{
        data(){
            return{dogs:[],buycheckboxs:[],
                    imgPath: require('@/assets/pizza.png'),
                    plusbutton: require('@/assets/app_button_plus.png'),
                    favorite: require('@/assets/627.png'),
                    boughtlist: require('@/assets/1569.png'),
                    history: require('@/assets/9655.png'),
                    deleteflg:false
            }
        },
        created(){
            this.dogs=JSON.parse(window.localStorage.getItem('dogs'))||[];
            //配列分画像を作る
            for (let k = 0; k < this.dogs.length; k++) {
                 this.dogs[k].id = require("@/assets/" + this.dogs[k].fname );
            }
        },
        methods:{
            deleteTodo(i){
                this.dogs.splice(i,1);
                window.localStorage.setItem('dogs',JSON.stringify(this.dogs));
            },
            goPageAdd(){
                this.$router.push('/todos/add');
            },
            goPageFavorite(){
                this.$router.push('/todos/favorite');
            },
            goPageCheckList(){
                this.$router.push('/');
            },
            goPageHistory(){
                this.$router.push('/todos/history');
            },
        buy(){
            // １．現在日時を取得
            let date = new Date()
            let dispDate = date.getMonth() + "/" + date.getDay() + " " + date.getHours() + ":" + date.getMinutes()
            
            // 保存された履歴と買い物リストを取得
            const rirekis=JSON.parse(window.localStorage.getItem('rirekis'))||[];
            this.dogs=JSON.parse(window.localStorage.getItem('dogs'))||[];
            
            // 買い物リストチェックボックスONの商品名配列を回す
            for (let k = 0; k < this.buycheckboxs.length; k++) {
                for (let m = 0; m < this.dogs.length; m++) {
                    // チェックされた商品をチェックリスト内から探す
                    
                    if (this.buycheckboxs[k] == this.dogs[m].name) {
                        // 購入履歴情報を作成
                        rirekis.unshift({id:"999",name:this.buycheckboxs[k],day:dispDate,fname:this.dogs[m].fname});
                        // チェックリスト情報から削除
                        this.dogs.splice(m, 1);
                    }
                }
            }
            // 編集した履歴と買い物リスト（削除済み）を保存
            window.localStorage.setItem('rirekis',JSON.stringify(rirekis));
            window.localStorage.setItem('dogs',JSON.stringify(this.dogs));
            
            this.$router.push('/todos/history');
			},
			clearAll(){
				//全ストレージデータをクリアする
				window.localStorage.removeItem("todos");
				window.localStorage.removeItem("favs");
				window.localStorage.removeItem("dogs");
				window.localStorage.removeItem("rirekis");
				this.$router.push('/todos/favorite');
			}
        }
    }
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+JP');

